.ToDo{
    padding: 10px;
    background-color: #57CC99;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    border-bottom: 4px  solid #22577A;
    font-size: 1.7rem;
    font-weight: bold;
}

.id{
    color: #0B0500;
    width: 5%;
    margin: auto 0;
}

.task{
    color: #0B0500;  
    width:75%;
    text-align: justify;
}

.checkBox{
    cursor: pointer;
    margin: auto 0;
}

.deleteBtn{
    margin: auto 0;
    width: 12%;
    height: 35px;
    text-align: center;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 10px;
    
}